export const spinnerMixin = {
  computed: {
    spinnerColor () {
      return this.color || this.$parent.color || 'red';
    },
    spinnerSize () {
      return (this.size || this.$parent.size || 50) + 'px';
    }
  },
  props: {
    color: String,
    size: Number
  }
};



