<template lang="html">
  <div class='snake' :style="{
    'border-top-color' : spinnerColor,
    'border-left-color' : spinnerColor,
    'border-bottom-color' : spinnerColor,
    'height' : spinnerSize,
    'width' : spinnerSize
    }">
  </div>
</template>

<script>
import {spinnerMixin} from '@/components/shaySpinner/common.js';
export default {
  name: 'snake',
  mixins: [spinnerMixin]
};
</script>

<style lang="css">
.snake {
  animation : shay-spinner-rotate 0.8s infinite linear;
  border : 4px solid transparent;
  border-radius : 50%;
}
@keyframes  shay-spinner-rotate {
  0% {
    transform : rotate(0deg);
    transform : rotate(360deg)
  }
}
</style>



